import { Component, OnInit } from '@angular/core';

@Component({
    selector: 'app-calc2',
    templateUrl: './calc2.component.html',
    styleUrls: ['./calc2.component.scss']
})
export class Calc2Component implements OnInit {

    valueInput: number = 1;
    claseIcon: string = 'iconBTC';
    valueSelect: string = 'Bitcoin';
    valueSlider: number = 30;
    coinType: string = 'BTC';

    constructor() { }

    ngOnInit(): void {
    }

    select() {
        switch (this.coinType) {
            case 'BTC':
                this.valueSelect = 'Bitcoin';
                this.claseIcon = 'iconBTC';
                break;
            case 'XBT':
                this.valueSelect = 'Bitcoin XBT';
                this.claseIcon = 'iconXBT';
                break;
            case 'ETH':
                this.valueSelect = 'Ethereum';
                this.claseIcon = 'iconETH';
                break;
            case 'XRP':
                this.valueSelect = 'Ripple XRP';
                this.claseIcon = 'iconXRP';
                break;
            case 'LTC':
                this.valueSelect = 'Litecoin';
                this.claseIcon = 'iconLTC';
                break;
            default:
                this.valueSelect = '';
                break;
        }
    }

    sum(min: number, max: number) {
        if (this.valueInput >= min && this.valueInput < max) {
            this.valueInput = this.valueInput + 1;
        }
    }

    rest(min: number, max: number) {
        if (this.valueInput > min && this.valueInput <= max) {
            this.valueInput = this.valueInput - 1;
        }
    }

}
